@use '../base/mixins' as mixin;

.review {
  padding: 32px 42.5px;
  border: 1px solid var(--secondary-light, #EEE);
  border-radius: 32px;

  @include mixin.small-laptop {
    padding: 24px;
  }

  &__head {
    margin-bottom: 32px;

    @include mixin.small-laptop {
      margin-bottom: 24px;
    }

    img {
      width: auto;
    }
  }

  &__content {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 32px;

    @include mixin.small-laptop {
      font-size: 12px;
    }
  }

  &__author {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__author-img {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__author-name {
    font-size: 14px;
    font-weight: 600;
    line-height: 1.2;

    @include mixin.small-laptop {
      font-size: 12px;
    }
  }

  &__author-role {
    font-size: 12px;
    font-weight: 400;
    line-height: 1.5;
  }
}