@use '../base/mixins' as mixin;

.banner-section {
  &--empty {
    text-align: center;
  }

  .h1-custom {
    margin-bottom: 16px;
  }

  &--empty .h1-custom {
    margin-bottom: 32px;

    @include mixin.small-tablet {
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--secondary-darker, #6a6a6a);
    margin-bottom: 32px;
    text-transform: uppercase;

    @include mixin.small-tablet {
      font-size: 12px;
      margin-bottom: 16px;
    }
  }

  .subtitle {
    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &--empty .subtitle {
    max-width: 624px;
    margin: 0 auto;
    margin-bottom: 32px;

    @include mixin.small-tablet {
      max-width: 450px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px 24px;

    @include mixin.phone {
      flex-direction: column;
      align-items: center;
    }
  }

  &--empty .banner-section__btns {
    flex-direction: column;
  }

  &__btn {
    min-width: 200px;

    @include mixin.phone {
      min-width: 100%;
    }
  }

  &--empty .banner-section__btn {
    @include mixin.phone {
      min-width: 200px;
    }
  }

  &__img {
    width: 100%;
  }

  &__info {
    color: var(--secondary-darker, #6a6a6a);
    display: flex;
    align-items: center;
    gap: 6px;

    span {
      font-size: 16px;
      font-weight: 400;
      line-height: 1.5;

      @include mixin.wide-tablet {
        font-size: 14px;
      }
    }
  }
}