@use '../base/mixins' as mixin;

@import '../blocks/_banner-section';
@import '../blocks/_investors';
@import '../blocks/_review';

.case {
  width: 100%;

  &__img {
    width: 100%;
    aspect-ratio: 1.73;
    margin-bottom: 16px;
    overflow: hidden;
    border-radius: 16px;
    background: var(--secondary-lighter, #f5f5f5);

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__title {
    font-weight: 600;
    line-height: 1.2;
  }
}

.case-reviews {
  &__head {
    margin-bottom: 48px;

    @include mixin.small-laptop {
      margin-bottom: 24px;
    }
  }
}