@use '../base/mixins' as mixin;

.investors {
  &__title {
    text-align: center;
    margin-bottom: 8px;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.5;
    text-transform: uppercase;
  }

  &__content {
    display: flex;
    overflow: hidden;
  }

  &__list {
    min-width: 100%;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    // animation: marqueeLoop 40s linear infinite;

    @include mixin.tablet {
      grid-template-columns: repeat(5, 1fr);
    }

    @include mixin.sm-phone {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &--marquee .investors__list {
    display: flex;
    gap: 10px;
    animation: marqueeLoop 40s linear infinite;
    
    @include mixin.tablet {
      animation: marqueeLoop 25s linear infinite;
    }

    @include mixin.sm-phone {
      animation: marqueeLoop 10s linear infinite;
    }
  }

  &__item {
    display: block;
    align-items: stretch;
    flex-grow: 1;

    img {
      display: block;
      width: 100%;
    }
  }

  &--marquee .investors__item {
    min-width: 128px;
  }

  @keyframes marqueeLoop {
    0% {
      transform: translateX(0);
    }

    100% {
      transform: translateX(-100%);
    }
  }
}